const validLen = (value: string): string | boolean => {
  return !!value || " ";
};

const validSign = (value: string): string | boolean => {
  // eslint-disable-next-line no-useless-escape
  const regExp = /[\{\}\[\]\/?.,;:|\)*~`!^\-_+<>@\#$%&\\\=\(\'\"]/g;
  return !regExp.test(value) || "   ";
};

export { validLen, validSign };
